.footer {
    height: 300px;
    background-color: lightyellow;
    display: flex;
    justify-content: center;
}

.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid;
    align-items: center;
}
.name{
    display: flex;
    justify-content: center;
    height: 30px;

}

.icons{
    display: flex;
    justify-content: space-around;
    width: 400px;

}
.icon{
    width: 40px;
    height: 40px;
    background-color: grey;
}